import React, { useState, useContext } from 'react';
import { Link } from 'react-router-dom';
import { GlobalContext } from '../context/GlobalState';
import './Lasts.css';

function Lasts () {
    const [color, setColor] = useState("blue")
    const { bag } = useContext(GlobalContext);


    const boxCli = (e) => {
        setColor({color: "red"})
    }
    return (
        <div className="lasts">
            <Link to="/" className="home">
                <p className="pc"><img src={`data:image/png;base64,`} alt="logo" /></p>
                <p className="pp">Home</p>
            </Link>
            <Link to="/Bag" className="bag">
                <div className="line">
                <p className="pc"><img src={`data:image/png;base64,`} alt="logo" /></p>
                <p className="pp">Bag</p>
                </div>
                <div className="headers">
                    <div class="numberings">{bag.length}</div>
                </div>
            </Link>
            <Link to="/Account" className="account" onClick={boxCli} style={{color: color}}>
                <p className="pc"><img src={`data:image/png;base64,`} alt="logo" /></p>
                <p className="pp">Account</p>
            </Link>
        </div>
    )
}

export default Lasts;