import React from 'react';
import './Account.css';
import { Link } from 'react-router-dom';

function Account () {
    return (
        <>
        <div className="container">
            <div className="head"><img src={`data:image/png;base64,`} alt="logo" /></div>
            <h4>Login</h4>
            <p className="pil">Please Login to view your Orders</p>
            <p className="pip">Select Country Code and Enter Your Phone number</p>
            <div className="no">
                <div>Phone Number</div>
                <div className="digit">
                    
                    {/* <div> */}
                         <input type="Phone" placeholder="00 0000000" />
                    {/* </div> */}
                </div>
            </div>

            <div className="verify">
                <Link to ='' className ='Verification'>
                    Send Verification Code
                </Link>
            </div>
        </div>
         <div className="lasts">
         <Link to="/" className="home">
             <p className="pc"><img src={`data:image/png;base64,`} alt="logo" /></p>
             <p className="pp">Home</p>
         </Link>
         <Link to="/Bag" className="bag">
             <div className="line"></div>
             <p className="pc"><img src={`data:image/png;base64,`} alt="logo" /></p>
             <p className="pp">Bag</p>
         </Link>
         <Link to="/Account" className="account">
             <p className="pc"><img src={`data:image/png;base64,`} alt="logo" /></p>
             <p className="pp">Account</p>
         </Link>
     </div>
     </>
    )
}

export default Account;